import { Component, OnInit, Input } from '@angular/core';
import { Storage } from '@ionic/storage';
import { calculatePercentage } from '../../util/math';

@Component({
  selector: 'app-list-row-view',
  templateUrl: './list-row-view.component.html',
  styleUrls: ['./list-row-view.component.scss'],
})
export class ListRowViewComponent implements OnInit {
  @Input() item;
  @Input() goldSpot;
  @Input() silverSpot;
  @Input() palladiumSpot;
  @Input() platinumSpot;

  constructor(
    private storage: Storage
  ) { }

  ngOnInit() {
    // console.log(calculatePercentage(12, 13));
  }

  calculatePercentage(itemType, b, itemQuantity = undefined) {
    let a = 0;
    // a: current worth; b: paid

    a = this.getRelevantSpot(itemType);

    // Calculate percentage:
    // ((WORTH - PAID) / PAID) * 100 = %
    a = a++;
    b = b++;

    // if this is a lot, we want to divide total price by lot amount
    if(itemQuantity) {
      b = b / itemQuantity;
    }

    if(typeof a === 'number') {
      return calculatePercentage(a, b);
      // return (((a - b) / b) * 100 * 2).toFixed(2);
    }
  }

  calculateDiff(itemType, b, itemQuantity = undefined) {
    let a = 0;
    // a: current worth; b: paid

    a = this.getRelevantSpot(itemType);

    a = a++;
    b = b++;

    // if this is a lot, we want to divide total price by lot amount
    if(itemQuantity) {
      b = b / itemQuantity
    }

    if(typeof a === 'number') {
      return ((a - b) * itemQuantity).toFixed(2);
    }
  }

  roiIndicator(itemType, paid, itemQuantity = undefined) {
    let ret;

    // if this is a lot, to diminish manipulations,
    // we're just looking at how much per item we paid
    paid = paid++ / itemQuantity++;

    switch(itemType) {
      case 'gold':
        ret = this.goldSpot > paid;
        break;
      case 'silver':
        ret = this.silverSpot > paid;
        break;
      case 'palladium':
        ret = this.palladiumSpot > paid;
        break;
      case 'platinum':
        ret = this.platinumSpot > paid;
        break;
    }

    return ret === true ? 'green' : 'red';
  }

  getRelevantSpot(itemType) {
    let a;

    switch(itemType) {
      case 'gold':
        a = this.goldSpot;
        break;
      case 'silver':
        a = this.silverSpot;
        break;
      case 'palladium':
        a = this.palladiumSpot;
        break;
      case 'platinum':
        a = this.platinumSpot;
        break;
    }

    return a;
  }
}
