import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { CollectionService } from '../../collection.service';
import { Storage } from '@ionic/storage';
import { MarketsService } from '../../markets.service';
import { interval } from 'rxjs';
import { environment } from '../../../environments/environment';

@Component({
  selector: 'app-list-view',
  templateUrl: './list-view.component.html',
  styleUrls: ['./list-view.component.scss'],
})
export class ListViewComponent implements OnInit {
  @Input() collection;
  @Output() filterList = new EventEmitter<string>();
  goldSpot: string;
  silverSpot: string;
  palladiumSpot: string;
  platinumSpot: string;
  subscriptionMarkets: any;
  private SPOT_REFRESH_STORAGE: number;
  // oneMinute: number;

  constructor(
    private collectionService: CollectionService,
    private storage: Storage,
    private marketsService: MarketsService
  ) {
    this.SPOT_REFRESH_STORAGE = environment.intervals.SPOT_REFRESH_STORAGE;
  }

  ngOnInit() {
    this.getSpot();

    const int = interval(this.SPOT_REFRESH_STORAGE);
    int.subscribe(() => {
      this.getSpot();
    });

  }

  getSpot() {
    this.storage.get('spot_xau')
      .then(val => this.goldSpot = JSON.parse(val)[0].price )
      .catch(err => console.error(err));

    this.storage.get('spot_xag')
      .then(val =>  this.silverSpot = JSON.parse(val)[0].price )
      .catch(err => console.error(err));

    this.storage.get('spot_xpt')
      .then(val =>  this.palladiumSpot = JSON.parse(val)[0].price )
      .catch(err => console.error(err));

    this.storage.get('spot_xpd')
      .then(val => this.platinumSpot = JSON.parse(val)[0].price )
      .catch(err => console.error(err));
  }

  searchListView(e) {
    const searchString = e.target.value.toLowerCase();
    console.log('TODO: search other fields, not just title');
    this.filterList.emit(searchString);

  }
}
