import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { Item } from '../collection-item.model';
import { CollectionService } from '../collection.service';
import { NewItemComponent } from '../components/new-item/new-item.component';
import { ToastController } from '@ionic/angular';
import { environment } from '../../environments/environment';
import { MarketsService } from '../markets.service';
import _has from 'lodash/has';
import _get from 'lodash/get';

@Component({
  selector: 'app-list',
  templateUrl: 'list.page.html',
  styleUrls: ['list.page.scss']
})
export class ListPage implements OnInit {
  collection: Item[];
  currentTime: any;
  updateInterval: number;
  logEnabled: boolean;
  marketStatus: boolean;
  subscriptionMarkets: any;

  constructor(
    private collectionService: CollectionService,
    public modalController: ModalController,
    public toastController: ToastController,
    private marketsService: MarketsService
  ) {
    this.logEnabled = environment.logEnabled;
  }

  ngOnInit() {
    this.updateInterval = 60000; // one minute
    this.currentTime = Date.now();

    this.updateCurrentTime(this.updateInterval);

    this.collection = this.collectionService.getAllItems();

    this.subscriptionMarkets = this.marketsService.marketsOpen().subscribe(res => {
      res ? this.marketStatus = true : this.marketStatus = false;
    });
  }

  updateCurrentTime(interval) {
    setInterval(() => {
      this.currentTime = Date.now();
    }, interval);
  }

  async presentModal() {
    const modal = await this.modalController.create({
      component: NewItemComponent
    });

    modal.onDidDismiss().then(data => {
      if(_has(data, 'data.returnedData')) {
        // this.collection.unshift({
        //   title: data.data.title.value,
        // })

        this.collection.unshift(  {
          id: '1154',
          title: _get(data, 'data.returnedData.title', ''),
          type: _get(data, 'data.returnedData.type', 'other'),
          form: _get(data, 'data.returnedData.form', 'other'),
          weight: _get(data, 'data.returnedData.weight', ''),
          purity: _get(data, 'data.returnedData.purity', ''),
          country: _get(data, 'data.returnedData.country', ''),
          quantity: _get(data, 'data.returnedData.quantity', '1'),
          paid: _get(data, 'data.returnedData.paid', ''),
          cert: _get(data, 'data.returnedData.cert', ''),
          certVendor: _get(data, 'data.returnedData.certVendor', ''),
          imageurl:
          _get(data, 'data.returnedData.imageurl', ''),
          notes: _get(data, 'data.returnedData.notes', ''),
          label: _get(data, 'data.returnedData.label', ''),
          labelColor: _get(data, 'data.returnedData.labelColor', ''),
          created: Date.now().toString(),
          updated: "",
        });

        this.presentToast(data.data.title.value);
      }
    });

    return await modal.present();
  }

  async presentToast(name) {
    const toast = await this.toastController.create({
      message: `Successfully added "${name}".`,
      duration: 3000
    });
    toast.present();
  }

  handleListUpdate(e) {
  }

  filterListUpdate(e) {
    this.collection = this.collectionService.filterItems(e.toLowerCase());
  }

}
